.banner {
  margin-top: 5px;
  background-image: url('./../../../images/background/banner.jpg');
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  width: 100%;
  height: 300px;
  box-shadow: 0px 2px 5px 1px rgba(0,0,0,0.3);
  border-radius: 5px;
}